import instance from "../../api/index.js"
export default {
	data() {
		return {
			btnnum: 0,
			showRight: false,
			showLeft: false,
			token:"",
			menuNo:'',
			list:[]
		};
	},
	onLoad(options) {
		console.log(options)
		this.token=uni.getStorageSync("token");
		this.menuNo=options.menuNo
		instance({
			url:"/api/product/queryProductByMenuNo",
			data:{
				token:this.token,
				menuNo:this.menuNo,
				start:0,
				limit:10
			},
			method:'POST',
			header:{
			   'content-type':'application/json',
			}
		}).then(res=>{
			console.log(res)
			this.list=res.data.data
		})
	},
	methods: {
		change(e) {
			this.btnnum = e
		},
		showDrawer(e) {
			this.$refs[e].open()
		},
		// 关闭窗口
		closeDrawer(e) {
			this.$refs[e].close()
		}
		
	}
}
